lay\" is a big textarea that mirrors the bytes stored in screen RAM. I've faked the clock oscillator (see below), but even so, 650Vue can run interrupt-driven 6502 machine-language programs as long as precise timing is not a requirement. Although 650Vue has no real-world analogue, it is a bit like a more user-friendly version of MOS Technology, Inc.'s "),_c('a',{attrs:{"href":"https://en.wikipedia.org/wiki/KIM-1"}},[_vm._v("KIM-1")]),_vm._v(" computer. ")]),_c('h2',[_vm._v("Timing, Interrupts, and the Virtual Clock")]),_c('p',[_vm._v(" 650Vue does not implement a precise clock. Instead, it will try to run 10,000 CPU cycles' worth of machine-language instructions on every browser repaint. The repaint typically occurs 60 times per second, so we can aspire to an effective clock rate of 600,000 CPU cycles per second, or 600kHz. However, the repaint rate tends to fluctuate, and a slow repaint or heavy garbage collection can put a serious dent in the browser's frame rate, causing the virtual CPU's effective clock speed to drop. 650Vue isn't really optimized for steady performance, but it can run programs at close to full speed if we're willing to tolerate small inaccuracies in the timing. ")]),_c('p',[_vm._v(" 650Vue can handle interrupt requests. On each browser repaint frame, after the CPU has burned through 10,000-ish cycles, it will check bit 2 of the status register—the interrupt disable flag. If interrupts are enabled, the CPU will jump to the routine pointed to by the 6502's IRQ vector and will resume normal execution when it encounters an RTI (Return From Interrupt) instruction. You need to be careful, though, because interrupt-driven routines can eat a LOT of your computer's RAM and CPU. If your machine gets hot and your fan starts running excessively, the STOP button will generate an NMI (Non-Maskable Interrupt) and immediately halt execution. ")])])])}]

